<script>
export default {
  name: "app",
  data() {
    var nodes = ["loacl", "dao"];

    return {
      nodeName: "local",
      nodes: nodes,
      loading: true,
      walletcrate: true
    };
  },
  mounted() {
    //
    this.$axios({
      method: "post",
      data: JSON.stringify({
        id: new Date().getTime(),
        method: "wallet_status",
        params: null
      })
    }).then(response => {
      this.loading = false;

      if (typeof response.data.error != "undefined") {
        //alert
        //reflash
        this.$alert("获取钱包信息异常，请刷新", "加载", {
          showClose: false,
          confirmButtonText: "确定",
          callback: action => {
            this.$router.go(0);
          }
        });
      } else {
        if (response.data.result.Stats == "nil") {
          //must make a count
          this.$alert("要先创建一个钱包", "创建钱包", {
            showClose: false,
            confirmButtonText: "确定",
            callback: action => {
              this.$router.push({ path: "/wallet/create" });
              this.$router.beforeEach((to, from, next) => {
                if (to.path != "/wallet/create") {
                  next("/wallet/create");
                }
              });
            }
          });
        } else {
          this.$store.state.accounts = response.data.result;
        }
      }
    });
  },
  methods: {
    wathStats() {}
  },
  watch: {
    needOneAccount: function() {
      if (this.$store.state.accounts.length == 0) {
        return false;
      }
    }
  }
};
</script>


